.campaignTable {
    margin-bottom: 50px;
    margin-top: 35px;
    width: 80%;
    margin-left: auto;
    margin-right: auto;
     height: calc(100% - 50px); 
    box-shadow: 0 2px 10px 0 rgba(0,0,0,0.1);
    padding-bottom: 100px
}

.tableHeader {
    background-color: #F1F1F4;
    height: 50px;
    width:100%;
    display: flex;
    align-items: center;
    justify-content: flex-start;
}

.tableRow {
    display: flex;
    align-items: center;
    justify-content: space-between;

}

.tableHeader > span {
    color: #556789;	
    font-size: 16px;	
    font-weight: 500;	

   
}
.tableHeader > span.date{
    margin-left: 2%;
}
.tableHeader > span.campains{
    margin-left: 16%;
}
.tableHeader > span.view{
    margin-left: 20%;
}




.tableHeader > span.action {
    margin-left: 20%;
}

@media screen and (max-width: 700px){
    .tableHeader{
        justify-content: space-evenly
    }
    .tableHeader > span {
        margin-left: 4%;
    }
    /* .tableHeader {
        width: 100vh;
    } */
    .campaignTable {
        width: 100%;
    }   
    .tableHeader > span.action {
        margin-left: 0;
    }

    .tableHeader > span {
    margin-left: 0;
    }
}