.App {
  overflow: hidden;

}
.heading {
  margin-top: 50px;
  height: 58px;
  color: #1F2640;
}
.heading > div {
  color: #1F2640;
  height: 100%;
  /* width: 230px; */
  font-weight: bold;
  line-height: 80px;
  font-size: 40px;
  width: 80%;
  margin-left: auto;
  margin-right: auto;
}

.campaigns-tabs {
 
  margin-top: 20px;
  width: 80%;
  margin-right: auto;
  margin-left: auto;
}



.separator {
  height: 1px;
  background-color: #F1F1F4;
  width: 80%;
  margin-left: auto;
  margin-right: auto;
  display: block
}
.switch {
  display: inline-block;
  margin-right: 40px;
  font-size: 20px;
  line-height: 28px;
  color: #556789;
  padding: 15px;
}
.switch.active {
  font-size: 20px;
  border-bottom: solid;
  border-width: 4px;
  color:  #83A515;
  font-weight: 600
}


@media screen and (max-width: 700px){
  .heading > div{width: 100%;}
  .campaigns-tabs {
      margin-top: 20px;
      width: 100%;
  }
  .switch{
      font-size: 25px;
      margin-right: 0px
  }
  
}

