header {
    background-color: #1F2640;
    width: 100%;
    height: 80px
}
header img {
    height: 80px;
}

.logo-container {
    display: flex;
    align-items: flex-end;
    margin: 0% 3%;
    flex-wrap: nowrap;
    width: 80%;
    margin-left: auto;
    margin-right: auto;
    height: 100%;
}

.langSelector {
 margin-left: 1300px;
    padding:10px;
    justify-content: right;
    align-items: right;
   
}

.logo-container div {
    height: 100%;
    display: flex;
    width: 69%;
    flex-wrap: wrap;
}



.logo-container div div:nth-of-type(1) {
    font-weight: 600;
    color: white;
    font-size: x-large;
    margin-top: 10px;
    margin-left: 10px;
}
.logo-container div div:nth-of-type(2) {
    color: forestgreen;
    font-size: initial;
    margin-top: -55px;
    margin-left: 11px;
}


